import React from 'react';
import EmojiObjectsIcon from '@material-ui/icons/EmojiObjects';
import NotificationsIcon from '@material-ui/icons/Notifications';
import LabelImportantIcon from '@material-ui/icons/LabelImportant';
import EditIcon from '@material-ui/icons/Edit';
import ArchiveIcon from '@material-ui/icons/Archive';
import DeleteIcon from '@material-ui/icons/Delete';

const Sidebar = ()=>{
    return(
        <>
        <div className="side-bar">
                <ul className="left-menu">
                    <div className="single-menu active">
                        <EmojiObjectsIcon /><li><a href="#">Notes</a></li>
                   </div>
                   <div className="single-menu">
                   <NotificationsIcon /><li><a href="#">Reminders</a></li>
                   </div>
                   <div className="single-menu">
                   <LabelImportantIcon /><li><a href="#">Important</a></li>
                   </div>
                   <div className="single-menu">
                   <EditIcon /><li><a href="#">Edit Labels</a></li>
                   </div>
                   <div className="single-menu">
                   <ArchiveIcon /><li><a href="#">Archives</a></li>
                   </div>
                   <div className="single-menu">
                   <DeleteIcon /><li><a href="#">Bin</a></li>
                   </div>
                </ul>
            </div>
        </>
    )
}

export default Sidebar;