import React from 'react';
import logo from '../images/google-keep.png';
import ReplayIcon from '@material-ui/icons/Replay';
import ViewListIcon from '@material-ui/icons/ViewList';
import SettingsIcon from '@material-ui/icons/Settings';
import MenuIcon from '@material-ui/icons/Menu';

const Header = ()=>{

    const openMenu = ()=>{
        alert("I am working dude");
    }

    return(
        <>
        <header className="header">
            <div className="logo-content">
            <MenuIcon className="menu-icon" />
            <img src={logo} alt="logo"></img>
            <p className="logo-slogan">Keep</p>
            </div>
            <input type="search" placeholder="Search" id="search-bar"/>
            <div className="right-menu">
                <ReplayIcon className="reload-icon"/>
                <ViewListIcon className="view-list-icon"/>
                <SettingsIcon className="setting-icon"/>
                <MenuIcon className="menu-icon" onClick={openMenu}/>
            </div>
        </header>
        </>
    )
}

export default Header;