import React, { useState } from 'react';
import AddAlertIcon from '@material-ui/icons/AddAlert';
import GroupAddIcon from '@material-ui/icons/GroupAdd';
import ColorLensIcon from '@material-ui/icons/ColorLens';
import ImageIcon from '@material-ui/icons/Image';
import ArchiveIcon from '@material-ui/icons/Archive';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import UndoIcon from '@material-ui/icons/Undo';
import RedoIcon from '@material-ui/icons/Redo';

const InputBox = (props)=>{

    const [input, setInput] = useState({
        title: "",
        content: "",
    });

    
    
    const pickInput = (event)=>{
        const {name,value} = event.target;
        setInput((prevData)=>{
            return {
                ...prevData,
                [name] : value,
            }
        })
    };

    const addData = ()=>{
        
        input.title=="" && input.content=="" ? alert("Error") :
            props.addNote(input);
            setInput({
                title:"",
                content:"",
            })
    }

    return(
        <>
                <div className="wrap">
                <input type="text"  placeholder="Title" id="title" autoComplete="off" name="title" value={input.title} onChange={pickInput} />
                <br></br>
                    <textarea type="text" rows="2" cols="" name="content" value={input.content}  placeholder="Take a note..." id="note" onChange={pickInput}></textarea>
                    <div className="editing-icons">
                    <AddAlertIcon />
                    <GroupAddIcon />
                    <ColorLensIcon />
                    <ImageIcon />
                    <ArchiveIcon />
                    <MoreVertIcon />
                    <UndoIcon />
                    <RedoIcon />
                    <button className="addBtn" onClick={addData}>Add</button>
                    <button className="closeBtn">Close</button>
                    </div>
                </div>
        </>
    )
}

export default InputBox;