import React from 'react';

const Note = (props)=>{

    const deleteNote = ()=>{
        props.delete(props.id);
    }
    return(
        <>
        <div className="note-wrap">
            <h1>{props.title}</h1>
            <p>{props.content}</p>
            <button className="closeBtn" onClick={deleteNote}>Delete</button>
        </div>
        </>
    )
}

export default Note;